/* Image Styling */
.imageProjects {
  max-height: 400px; /* Limits the maximum height of the image */
  width: 100%; /* Allows the image to fill its container */
  height: auto; /* Allows the image to resize proportionally */
  object-fit: cover; /* Ensures the image covers the container without distortion */
  border: 5px solid rgba(255, 255, 255, 0.5); /* Adds a semi-transparent border */
  box-shadow: 0 8px 16px rgba(0, 0, 0, 0.2); /* Adds a subtle shadow for depth */
  transition: transform 0.3s ease-in-out; /* Smooth transition for hover effect */
  position: relative; /* Position the image relative to its container */
  z-index: 10; /* Ensure the image stays above the circles initially */
}

/* Circles Styling */
.circle {
  border-radius: 50%;
  position: absolute;
  opacity: 0.8; /* Subtle opacity to blend circles with the background */
  transition: transform 0.3s ease-in-out; /* Adds smooth transition to circles */
  transform-origin: center center; /* Ensure rotation happens around the circle's center */
}

/* Define the animation keyframes for orbiting circles with 3D effect */
@keyframes orbit-left {
  0% {
      transform: translate(-50%, -50%) rotate(0deg) translateX(-150px) rotate(0deg) translateZ(0);
  }
  50% {
      transform: translate(-50%, -50%) rotate(180deg) translateX(-150px) rotate(-180deg) translateZ(-200px);
      z-index: -1; /* Move behind the image */
  }
  100% {
      transform: translate(-50%, -50%) rotate(360deg) translateX(-150px) rotate(-360deg) translateZ(0);
      z-index: 1; /* Move in front of the image */
  }
}

@keyframes orbit-right {
  0% {
      transform: translate(-50%, -50%) rotate(0deg) translateX(150px) rotate(0deg) translateZ(0);
  }
  50% {
      transform: translate(-50%, -50%) rotate(180deg) translateX(150px) rotate(-180deg) translateZ(-200px);
      z-index: -1; /* Move behind the image */
  }
  100% {
      transform: translate(-50%, -50%) rotate(360deg) translateX(150px) rotate(-360deg) translateZ(0);
      z-index: 1; /* Move in front of the image */
  }
}

/* Apply the orbit animation to circles */
.circle-left {
  width: 140px;
  height: 140px;
  left: 50%; /* Center horizontally */
  top: 50%; /* Center vertically */
  background-color: rgba(240, 108, 203, 0.7); /* Adjusted opacity for better blending */
  animation: orbit-left 10s linear infinite; /* Orbiting animation */
}

.circle-right {
  width: 120px;
  height: 120px;
  left: 50%; /* Center horizontally */
  top: 50%; /* Center vertically */
  background-color: rgba(105, 236, 100, 0.7); /* Adjusted opacity for better blending */
  animation: orbit-right 12s linear infinite; /* Orbiting animation with different timing */
}

/* Responsive adjustments for mobile */
@media (max-width: 768px) {
  .imageProjects {
      max-height: 300px; /* Adjust max height for mobile */
      width: 100%; /* Allow the image to fill its container */
      height: auto; /* Maintain aspect ratio */
      object-fit: cover; /* Ensure the image covers the container without distortion */
  }

  .circle-left,
  .circle-right {
      width: 100px; /* Smaller circles for mobile */
      height: 100px;
  }
}


/* Header Styles */
header {
  background-color: transparent; /* Keep the background transparent */
  box-shadow: 0 2px 0px rgba(0, 0, 0, 0.1); /* Add a slight shadow to the bottom */
  position: relative; /* Allows for more control over positioning */
  z-index: 1; /* Ensure header stays on top */
}

/* Navigation Link and Button Styles */
header a {
  color: #000; /* Dark text for visibility */
  transition: color 0.3s ease; /* Smooth transition for text color */
}

